.invalidar-cita {
	text-align: center;
	margin: $separacion / 2 0;
}

.contenido-resumen {
	margin-bottom: $separacion / 2 0;
	p {
		margin-bottom: .5rem;
		span {
			font-weight: $bold;
			color: $azul;
			font-size: 2.4rem;
		}
	}

	.resumen-servicios {

		h3 {
			margin: $separacion / 2 0;
		}

		.contenedor-servicio {
			
			margin-bottom: 2rem;
			padding-bottom: 2rem;
			border-bottom: 1px solid #43453c;

			.precio {
				font-size: 2rem;
				font-weight: $bold;
				color: $azul;
			}
		}
	}
}

.total {
	text-align: right;
}