/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box; }

*, *::before, *::after {
  box-sizing: inherit; }

body {
  font-size: 1.6rem;
  background-color: #1A1B15; }

p {
  color: #FFFFFF;
  font-size: 2rem;
  font-family: "Lato", sans-serif; }

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto; }

a {
  text-decoration: none; }

img {
  max-width: 100%; }

h1, h2, h3 {
  margin: 0 0 2.5rem 0;
  color: #0E92D4;
  text-align: center; }

h1 {
  font-family: "Acme", sans-serif;
  font-size: 4.4rem;
  font-weight: 700;
  text-shadow: 0.1rem -0.2rem 0.4rem #000, 0.1rem 0.1rem 0.1rem #FFF; }

h2 {
  font-size: 3.6rem;
  text-shadow: 0.1rem -0.2rem 0.4rem #000, 0.1rem 0.1rem 0.1rem #FFF;
  font-family: "Acme", sans-serif; }

h3 {
  font-size: 3rem;
  text-align: center;
  text-shadow: 0.1rem -0.2rem 0.4rem #000, 0.1rem 0.1rem 0.1rem #FFF;
  font-family: "Acme", sans-serif; }

@media (min-width: 768px) {
  .contenedor-estetica {
    height: 100vh;
    display: grid;
    gap: 2.5rem;
    grid-template-columns: repeat(2, 1fr); } }

.contenedor-estetica .imagen {
  height: 35rem;
  background-image: url(../img/1.jpg);
  background-size: cover;
  background-position: center center; }
  @media (min-width: 768px) {
    .contenedor-estetica .imagen {
      height: auto; } }

.app {
  width: 90%;
  margin: 0 auto;
  padding-top: 2.5rem; }
  .app .seccion {
    display: none;
    margin-top: 2.5rem; }

.mostrar-seccion {
  display: block !important; }

.tabs {
  padding: 1.5rem 0;
  display: grid;
  gap: 1.3rem;
  grid-template-columns: repeat(3, 1fr); }
  .tabs button {
    background-color: #E1E1E1;
    font-size: 1.4rem;
    font-family: "Roboto", sans-serif;
    font-weight: 700;
    padding: 1rem;
    border: 0.1rem solid #1A1B15;
    text-transform: uppercase; }
    .tabs button.actual {
      background-color: #0E92D4;
      color: #FFFFFF; }
    .tabs button:hover {
      cursor: pointer; }

@media (min-width: 768px) {
  .listado-servicios {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr); } }

.listado-servicios .servicio {
  margin-bottom: 2rem;
  background-color: #FFFFFF;
  padding: 1rem;
  border-radius: 1rem;
  text-align: center; }
  @media (min-width: 768px) {
    .listado-servicios .servicio {
      margin-bottom: 0; } }
  .listado-servicios .servicio .nombre-servicio {
    color: #1A1B15;
    font-size: 2rem;
    font-family: "Lato", sans-serif;
    font-weight: 700; }
  .listado-servicios .servicio .precio-servicio {
    color: #0E92D4;
    margin: 0;
    font-size: 2rem;
    font-weight: 700;
    font-family: "Roboto", sans-serif; }
  .listado-servicios .servicio.seleccionado {
    background-color: #0E92D4; }
    .listado-servicios .servicio.seleccionado p {
      color: #FFFFFF; }

.servicio-subtitulo {
  text-transform: uppercase; }

.paginacion {
  padding-top: 1.8rem;
  display: flex;
  justify-content: space-between; }
  .paginacion button {
    background-color: #0E92D4;
    padding: .5rem 1rem;
    display: block;
    text-align: center;
    color: #FFFFFF;
    border: none;
    font-weight: 700;
    font-family: "Roboto", sans-serif;
    font-size: 1.6rem;
    text-transform: uppercase; }
    .paginacion button.ocultar {
      visibility: hidden; }
    .paginacion button:hover {
      cursor: pointer; }

.formulario {
  width: 100%; }
  .formulario .campo {
    display: flex;
    align-items: center;
    margin-bottom: 2rem; }
    .formulario .campo label {
      flex-basis: 7rem;
      color: #FFFFFF; }
    .formulario .campo input {
      flex-grow: 1;
      padding: .5rem;
      border-radius: .8rem;
      border: none; }

.alerta {
  padding: 1.25rem;
  text-align: center;
  color: #FFFFFF;
  width: 100%;
  display: block;
  font-size: 1.8rem; }
  .alerta.error {
    background-color: #a31010; }

.invalidar-cita {
  text-align: center;
  margin: 2.5rem 0; }

.contenido-resumen {
  margin-bottom: 2.5rem 0; }
  .contenido-resumen p {
    margin-bottom: .5rem; }
    .contenido-resumen p span {
      font-weight: 700;
      color: #0E92D4;
      font-size: 2.4rem; }
  .contenido-resumen .resumen-servicios h3 {
    margin: 2.5rem 0; }
  .contenido-resumen .resumen-servicios .contenedor-servicio {
    margin-bottom: 2rem;
    padding-bottom: 2rem;
    border-bottom: 1px solid #43453c; }
    .contenido-resumen .resumen-servicios .contenedor-servicio .precio {
      font-size: 2rem;
      font-weight: 700;
      color: #0E92D4; }

.total {
  text-align: right; }
